* {
  box-sizing: border-box;
}

.App {
  height: 100%;
  width: 100vw;
  display: grid;
  grid-template-areas: "Navbar SearchBar SearchBar SearchBar"
                       "Navbar Main Main Main"
                       "Player Player Player Player";
  grid-template-columns: 0.5fr repeat(3, 1fr);
  grid-template-rows: 40px 1fr 102px;
  background-color: #100e17;
  /* background-color: rgb(0, 5, 56); */
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}


.SearchBar {
  grid-area: SearchBar;
  display: flex;
  align-items: center;
  height: 40px;
  width: auto;
  background-color: #17141d;
  /* background-color: rgba(0, 0, 0, 0); */
}

.SearchBar input {
  width: 300px;
  padding: .30rem 0;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
  margin-left: 33%;
}

.SearchBar .Search_Button {
  position: relative;
  right: 27px;
  color: rgb(0, 8, 33);
}

.Navbar-wrapper {
  grid-area: Navbar;
  height: 100%;
  background-color: rgb(9, 99, 10);
  /* background-color: rgb(0, 8, 33); */
}

.Navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 400px;
  margin: 9rem 0 auto 1rem;
}

a.LinkButton  {
  text-decoration: none;
  text-align: left;
  color: white;
}

a.active.LinkButton {
  color: rgb(247, 9, 9);
}

.Playlist {
  align-items: center;
  overflow-y: scroll;
  width: 90%;
  max-height: 670px;
  margin: 1rem auto;
  font-size: 16px;
  font-weight: 100;
}

.Playlist input {
  width: 100%;
  border: 0;
  outline: 0;
  background: transparent;
  font-family: 'Poppins', sans-serif;
  font-size: 1.5rem;
  color: #fff;
}


.TrackList {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}


.Track {
  display: flex;
  position: relative;
  cursor: pointer;
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  width: 100%;
}

.Track:hover {
  box-shadow: 0px 5px 8px 2px rgb(0, 0, 0.7);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.Track-information img {
  height: 60px;
  margin: 10px;
}

.Track-information {
  display: flex;
  align-items: center;
  width: 100%;
}

.Album-img {
  flex-grow: 0.5;
}

.Track-name {
  flex-grow: 6;
}

.Track-information .Time {
  flex-grow: 1;
}

.Text {
  margin: auto 0;
}

.Text p {
  font-size: 0.67rem;
  margin: 8px 0;  
  white-space: nowrap;
  width: 130px;
  overflow: hidden;
  text-overflow: ellipsis;
}

p:last-child {
  font-weight: 100;
}

.Time {
    font-size: 14px;
    margin: auto;
    width: 40px;
    font-weight: 100;
}
.Action-Overlay {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  -webkit-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.Action-Overlay:hover {
  opacity: 1;
}


.Action-Overlay .artist {
  border-radius: 50%;
}

.play-pause {
  border: 1px solid grey;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

.play-pause:hover {
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.AddButton {
  border: 1px solid grey;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

.AddButton:hover {
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.DeleteButton {
  border: 1px solid grey;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

.DeleteButton:hover {
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.Grid {
  display: grid;
  grid: repeat(auto-fit, minmax(-webkit-max-content, 20%)) / repeat(auto-fit, minmax(50%, auto));
  grid: repeat(auto-fit, minmax(max-content, 20%)) / repeat(auto-fit, minmax(50%, auto));
  height: auto;
  width: 100%;
  padding: 2%;
}
button.Button {
  cursor: pointer;
  padding: .40rem 0;
  color: rgba(255, 255, 255, 0.6);
  font-size: .833rem;
  float: right;
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

button.Button:hover {
  color: white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


.Main {
  grid-area: Main;
  height: 100%;
  background-color: #17141d;
  /* background-color: rgb(0, 5, 56); */
  height: 715px;
  overflow-y: scroll;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.SearchResults {
  width: 100%;
  max-height: 705px;
  padding: 0 3rem;
  overflow-y: scroll;
  z-index: 999;
}

.Tracklist {
  display: flex;
  width: 100%;
}

.Tracklist-tracks {
  width: 55%;
  margin: 2rem 1rem;
}




.ResultsTracklist {
  justify-self: stretch;
  width: auto;
}
.Header {
  width: 80%;
  margin: 45px auto 0 auto;
}
.Title {
  display: inline-block;
  font-size: 20px;
  color: rgba(255, 255, 255, 0.9);
  cursor: default;
}
.Border {
  border-top: 1px solid rgba(255, 255, 255, 0.6);
  opacity: 0.7;
  max-width: 100%;
  height: 1px;
  margin: 0.5rem;
}

.PlaylistDisplay {
  display: flex;
  width: 100%;
  justify-items: flex-start;
  flex-wrap: wrap;
}
.Card {
  position: relative;
  width: 150px;
  height: 150px;
  margin: 1.5rem;
  background-color: rgba(0, 0, 20, 0.7);
  /* box-shadow: 0 0 3rem #000, 0 0 1rem #072; */
  /* box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.8), 0px -8px 25px rgba(0, 0, 0, 0.6); */
  box-shadow: 0 24px 38px 3px rgba(0,0,0,0.50), 0 9px 46px 8px rgba(0,0,0,0.30), 0 11px 15px -7px rgba(0,0,0,0.70);
    /* box-shadow: 0 19px 38px rgba(0,0,0,0.60), 0 15px 12px rgba(0,0,0,0.44); */
  -webkit-transition: all 200ms;
  transition: all 200ms;
  text-align: center;
}

.Card:hover {
  box-shadow: 0px 5px 30px 8px rgb(0, 0, 0, 0.5), 0px 5px 15px 4px rgb(0, 0, 0, 0.8);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.Card-Img {
  height: 150px;
  width: 150px;
}

.Card-Img img {
  max-height: 100%;
  max-width: 100%;
  margin-top: auto;
}

.artist, 
.artist img{
  border-radius: 50%;
}




.Player {
  grid-area: Player;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #100e17;
  /* background-color: rgb(3, 0, 51); */
  height: 100px;
  width: 100%;
  padding: 0 2rem;
}

.Track-info {
  width: 30%;
}

.Player .Control {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40%;
  height: auto;
}

.Player .Player-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 35%;
}

.Control-Leftside {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 30%;
  height: 100%;
}

.volume {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 50%;
}

.volume-bar {
  width: 5rem;
  margin: 0 0.22rem;
}

.Track-info .Track:hover {
  box-shadow: none;
  -webkit-transform: none;
          transform: none;
}

.Track-Bar {
  width: 80%;
  margin: 0 10px;
}

.Current-Playlist-Container {
  position: relative;
  width: 350px;
  height: 400px;
  background-color: rgb(0, 8, 33);
  right: -210px;
  bottom: 250px;
  overflow-y: scroll;
}

.Player .Track-information {
  margin: 0 5px;
}

.Player .Track-information img,
.Player .Action-Overlay {
  display: none;
}

.Bar-container {
  display: flex;
  position: relative;
  align-items: center;
  height: 100%;
  width: 100%;
  
}

.Bar {
  height: 3px;
  background-color: yellow;
  width: 100%;
  opacity: 0.5;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 10px;  
  margin: 0;
  z-index: 1;
}

input[type=range]::-webkit-slider-thumb {
  height: 20px;
  width: 20px;
  border-radius: 5px;
  background: yellow;
  cursor: pointer;
  -webkit-appearance: none;
  opacity: 0;
  z-index: 99;
  margin: 0 2px; 
} 


.progression {
  position: absolute;
  height: 4px;
  background-color: yellow;
  border-radius: 10px;
  z-index: 1;
}







.play-pause,
.play-pause:focus {
  border: 1px solid grey;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transition: all 300ms;
  transition: all 300ms;
}

.play-pause:hover {
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.back,
.forward {
  width: 40px;
  -webkit-transition: 300ms;
  transition: 300ms;
}

.back:hover,
.forward:hover {
  width: 40px;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.back,
.forward {
  width: 40px;
  -webkit-transition: 300ms;
  transition: 300ms;
}

.back:hover,
.forward:hover {
  width: 40px;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.Playlist-Button {
  opacity: 0.5;
  -webkit-transition: 300ms;
  transition: 300ms;
}

.Playlist-Button:hover {
  opacity: 1;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.Track-Progression {
  display: flex;
  width: 100% ;
  align-items: center;
}


